export default {
	data () {
		return {
			phone: '', //手机
			code: '',  //输入的验证
			newPwd: '', //新密码
			pwd: '',    //密码
			yzm: '',	//短信验证码
			getYzm: true,
			computedTimer: 0, 
			status: '' ,//判断是从哪来的 0忘记密码 1修改密码
			flag: ''
		}
	},
	onLoad(options) {
		this.status = options.status
		this.phone = uni.getStorageSync('user_phone')
		if (this.status == 0) {
			this.flag = true
			uni.setNavigationBarTitle({
			    title: '忘记密码'
			});
		} else if (this.status == 1) {
			this.flag = false
			uni.setNavigationBarTitle({
			    title: '修改密码'
			});
		}
	},
	onUnload(){
		
	},
	methods:{
		//提交
		async confirm () {
			if (this.phone.trim() == '' || this.code.trim() == '' || this.newPwd == '' || this.pwd == '') {
				uni.showToast({
					icon:'none',
					title:'请填写信息后进行更改'
				})
				return
			}
			if (this.newPwd != this.pwd) {
				uni.showToast({
					icon:'none',
					title: '两次输入的密码不一致'
				})
				return
			}
			
			// if (this.yzm !== this.code) {
			// 	uni.showToast({
			// 		icon:'none',
			// 		title: '验证码输入错误'
			// 	})
			// 	return
			// }
			
			let res = await this.$fetch(this.$api.customerForget,{mobile: this.phone,checkCode: this.code,password: this.newPwd},'POST','form')
			console.log(res)
			if (res.code === 0) {
				uni.showToast({
					icon: 'success',
					title: '修改成功'
				})
				uni.removeStorageSync('user_phone')
				uni.removeStorageSync('token')
				setTimeout(() => {
					uni.reLaunch({
						url: '../enter/enter'
					})
				},1500)
			} else {
				uni.showToast({
					icon: 'none',
					title: res.msg
				})
			}
		},
		//验证码
		async getCode () {
			if (!this.getYzm) return
			
			if(this.phone.trim() == '' ) {
				uni.showToast({
					icon: 'none',
					title: '请先输入手机号'
				})
				return
			} else if (this.phone.length != 11 ) {
				uni.showToast({
					icon: 'none',
					title: '请检查手机号输入是否正确'
				})
				return
			}
			this.getYzm = false 
			this.computedTimer = 60
			const timer = setInterval(() => {
				if (this.computedTimer === 0) {
					clearInterval(timer)
					this.getYzm = true
				} else {
					this.computedTimer--
				}
			},1000)
			let res = await this.$fetch(this.$api.customerPhone,{mobile:this.phone},'GET','form')
		
			this.yzm = res.msg
		},
	}
}
